*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background: linear-gradient(to left, rgb(1, 61, 100), rgb(64, 128, 128)); */
  background: -webkit-gradient(linear, right top, left top, from(rgb(134, 64, 97)), to(rgb(46, 119, 111)));
  background: -webkit-linear-gradient(right, rgb(134, 64, 97), rgb(46, 119, 111));
  background: linear-gradient(to left, rgb(134, 64, 97), rgb(46, 119, 111));
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}




#card {
  background: rgba(255, 255, 255, 0.897);
}

#card img {
  margin-bottom: 20px;
  width: 200px;
  height: auto;
  
}

h2{
  margin: 20px;
}

p{
  margin: 20px;
}



.input{
  margin-top: -40px; 
  margin-bottom: 20px;
}

.input input{
  background: rgba(24, 24, 24, 0.904);
  border: 2px solid black;
  color: rgb(255, 255, 255);
  border-radius: 50px;
  width: 300px;
  height: 40px;
  outline: none;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

