  padding: 10px;
  margin-top: 100px;
  margin-bottom: 100px;
  
}
h6{
  padding: 10px;
  margin-top: -20px;
  margin-bottom: 40px;
  font-size: 16px;
  text-transform: capitalize;
  font-weight: 100;
}

